import './style.css';
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import XYZ from 'ol/source/XYZ';
import BING from 'ol/source/BingMaps';
import * as ol from 'ol/proj';

//geolocation
import GEO from 'ol/Geolocation';

//vector layer
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import GeoJSON from 'ol/format/GeoJSON';


//add mouse control library
import MousePosition from 'ol/control/MousePosition';
import {createStringXY} from 'ol/coordinate';
import {FullScreen, defaults as defaultControls} from 'ol/control';

//add mousecontrol function
const mousePositionControl = new MousePosition({
  coordinateFormat: createStringXY(4),
  projection: 'EPSG:4326',
});

//add FullScreen function
const fullScreen = new FullScreen();

const attr = 'CSE5006 ' ;
  

const osm = new OSM({
  attributions: attr+'<a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>',
});

const googlemaps = new XYZ({
  //choose one for your basemap
    //default
    //url:"https://mt1.google.com/vt/lyrs=m&x={x}&y={y}&z={z}",  

    //terrain
    //url:"http://mt0.google.com/vt/lyrs=p&hl=en&x={x}&y={y}&z={z}", 

    //satellite
  // url:"http://mt0.google.com/vt/lyrs=s&hl=en&x={x}&y={y}&z={z}", 

    //Hybrid
   url: "http://mt0.google.com/vt/lyrs=y&hl=en&x={x}&y={y}&z={z}",    
  attributions: "Google Maps"
});

const cartodb = new XYZ({
  url:"https://cartodb-basemaps-a.global.ssl.fastly.net/light_all/{z}/{x}/{y}.png",
  attributions: "From CartoDB",
});

const esri = new XYZ({
  url:"https://server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}",            
  attributions: "ESRI",
});

const bing = new BING({
  key:"Ai5uhJ7V8w8cnyoDBY5tt_A5b-2343USu4G4vwhs_JTZGp3yQEDoJqrLBk1bcO-f",
  imagerySet: "CanvasDark" //Road, CanvasDark, CanvasGray, Aerial, AerialWithLabels
});

const geolocation = new GEO();

const ltu = ol.transform([145.04750,-37.72097], 'EPSG:4326', 'EPSG:3857');

const melbourne = new VectorLayer({
  source: new VectorSource({
    url: 'https://kadhinugraha.github.io/vector/melbourne.geojson',
    format: new GeoJSON(),
  }),
});

const fuel = new VectorLayer({
  source: new VectorSource({
    url: 'https://kadhinugraha.github.io/vector/fuel.geojson',
    format: new GeoJSON(),
  }),
});  

const train = new VectorLayer({
  source: new VectorSource({
    url: 'https://kadhinugraha.github.io/vector/train.geojson',
    format: new GeoJSON(),
  }),
});  

const basemap = new TileLayer({
  source: osm
});

const map = new Map({
  controls: defaultControls().extend([mousePositionControl,fullScreen]),
  layers: [basemap,melbourne,train],
  target: 'map',
  view: new View({
    center: ltu,
    zoom: 8
  })
});



